import {json, formdata} from '../common/js/axios'

// 
export function checkPhone(phone) {
    const url = `/oauth/checkPhone`

    return formdata.post(url, {phone})
}

/***
 * 
 * @param type
 * @param phone
 * @return {AxiosPromise<any>}
 */
export function getValidate(type, phone) {
    const url = `/oauth/getValidateCode`

    return formdata.post(url, {type, phone})
}

/***
 * 
 * @param data
 * @param data.phone  
 * @param data.code   
 * @param data.pwd    
 * @param data.name   
 * @returns {*}
 */
export function register(data) {
    const url = `/oauth/registerUser`

    return json.post(url, data)
}

/***
 * 
 * @param data
 * @param data.phone  // 
 * @param data.pwd    // 
 * @returns {*}
 */
export function login(data) {
    const url = `/oauth/login`

    return json.post(url, data)
}

/***
 * 
 * @returns {AxiosPromise<any>}
 */
export function loginOut() {
    const url = `/oauth/logout`

    return json.post(url)
}

/***
 * 
 * @param data
 * @param data.code  // 
 * @param data.phone // 
 * @param data.pwd   // 
 * @returns {AxiosPromise<any>}
 */
export function resetPassword(data) {
    const url = `/oauth/resetPwd`

    return formdata.post(url, data)
}

/***
 * 
 * @param data
 * @return {*|AxiosPromise<any>|void}
 */
export function resetPhone(data) {
    const url = '/oauth/resetPhone'

    return json.post(url, data)
}



