import Vue from 'vue'
import ShelterComponent from './shelter'

const ShelterController = Vue.extend(ShelterComponent)

let instance

const Shelter = (options = {}) => {
    if (Vue.prototype.$isServer) return

    let userOnClose = options.onClose

    let id = 'shelter_component'

    options.onClose = function () {
        Shelter.close(userOnClose)

        instance = undefined
    }

    instance = new ShelterController({
        data: options
    })

    instance.id = id

    instance.vm = instance.$mount()

    if (options.element) {
        options.element.appendChild(instance.vm.$el)
    } else {
        document.body.appendChild(instance.vm.$el)
    }


    instance.vm.visible = true

    instance.dom = instance.vm.$el

    return instance.vm
}

Shelter.show = options => {
    return Shelter(options)
}

Shelter.close = userOnClose => {
    instance && instance.close()

    typeof userOnClose === 'function' && userOnClose()
}

export default Shelter



