import {json as instance} from '../common/js/axios'
import Toast from 'vant/lib/toast'


/***
 * 
 * @param data
 * @param data.StartCoordinate 
 * @param data.GoalCoordinate 
 * @param data.Vehicle 
 * @return {*}
 */
export function getNavigationRoute(data) {
    const url = `/route`

    Toast({
        position: 'center',
        type: 'loading',
        message: ' ...',
        duration: 0
    })

    return instance.post(url, data).then(res => {
        try {
            res = JSON.parse(res)
        } catch (err) {
            console.log(err)
        }

        Toast.clear()
        return Promise.resolve(res)
    })
}


/***
 * 
 * @param data
 * @param data.centerPoint.longitude
 * @param data.centerPoint.latitude
 * @param data.distance  
 * @param data.types  
 * @return {*}
 */
export function getNavigationPoint(data) {
    const url = `/search/geofilt`

    return instance.post(url, data)
}

export function getNavigationals(data) {
    const url = `/search/channel`

    return instance.post(url, data)
}



