<template>
    <transition name="shelter">
        <div class="shelter" v-show="visible" @click="onClick"
             :style="{
                 backgroundColor: backgroundColor,
                 zIndex: zIndex,
             }"></div>
    </transition>
</template>

<script>
    export default {
        name: "shelter",
        data() {
            return {
                visible: false,
                closed: true,
                onClose: null,
                zIndex: 2000,
                backgroundColor: 'rgba(0, 0, 0, 0.33)'
            }
        },
        methods: {
            destroyElement() {
                this.$el.removeEventListener('transitionend', this.destroyElement)
                this.$destroy(true)
                this.$el.parentNode.removeChild(this.$el)
            },
            close() {
                this.visible = false
            },
            onClick() {
                if (this.closed) {
                    this.close()
                }
                this.$emit('click', this.$el)
            }
        },
        watch: {
            visible(value) {
                if (!value) {
                    if (typeof this.onClose === 'function') {
                        this.onClose(this)
                    }

                    this.$el.addEventListener('transitionend', this.destroyElement)
                }
            }
        }
    }
</script>

<style lang="stylus">
    .shelter
        position fixed
        top 0
        left 0
        right 0
        bottom 0
</style>



