import * as map from '../cordova/map'

// 
export const MapMixins = {
    methods: {
        // 
        reload() {
            return map.getMapReload()
        },
        // 
        getLocation() {
            return map.getMapLocation()
        },
        // 
        enable(enable) {
            return map.setMapEnable(enable)
        },
        // 
        visibility(visible) {
            return map.setMapVisibility(visible)
        },
        // 
        zoom() {
            return map.setMapZoom()
        },
        // 
        enlarge() {
            return map.setMapZoomIn()
        },
        // 
        reduce() {
            return map.setMapZoomOut()
        },
        // 
        getScale() {
            return map.getMapScale()
        },
        // 
        setScale() {
            return map.setMapScale()
        },
        // 
        setCenter(x, y, scale) {
            return map.setMapCenterAt(x, y, scale)
        },
        // 
        getCenter() {
            return map.getMapCenter()
        },
        // 
        getContrastPoint({x, y}, {maxX, maxY, minX, minY}) {
            return x < maxX && x > minX && y < maxY && y > minY
        },
        // 
        screenToLocation(x, y) {
            return map.screenToLocation(x, y)
        },
        // 
        locationToScreen(x, y) {
            return map.locationToScreen(x, y)
        },
        // 
        style() {
            return map.getStyle()
        },
        // 
        setStyle(style) {
            return map.setStyle(style)
        },
        // 
        // setElementList(data) {
        //     for (let key in data) {
        //         if (key === 'beacon' && isArray(data.beacon).length) {
        //             map.listNavInfos(data.beacon)
        //         }
        //         else if (key === 'water' && isArray(data.water).length) {
        //             map.listWaterStationInfos(data.water)
        //         }
        //         else if (key === 'ship' && isArray(data.ship).length) {
        //             map.listAISInfos(data.ship)
        //         }
        //         else if (key === 'build' && isArray(data.build).length) {
        //             map.listRegulationInfos(data)
        //         }
        //     }
        // },

        // 
        setLayersVisible(arr = [], visible) {
            const index = arr[0]


        },
        // 
        positionElement(id, type, items) {
            return map.positioningElement(id, type, items)
        },
        // 
        pathPlanning(start, end) {
            return map.pathPlanning(start, end)
        },
        // 
        speak(message) {
            map.speak(message)
        },
        // 
        setSpeaker(theme) {
            map.setSpeaker(theme)
        },
        // 
        setMapVisualAngle(visible) {
            if (visible) {
                return map.setMapTruenorth()
            } else {
                return map.setMapCompassMode()
            }
        },
        // 
        setMapDepth(data) {
            return map.setMapAirworthiness(data)
        },
        // 
        getMapExtent() {
            return map.getMapExtent()
        },
        changeMapChartLayer(visible) {
            return map.changeChartLayerVisible(visible)
        },
        changeMapTieldLayer(visible) {
            return map.changeTieldLayerVisible(visible)
        },
    }
}



