<template>
    <div class="no-success" :style="{height: height}">
        <div>
            <slot></slot>
        </div>
        <p>{{text}}</p>
    </div>
</template>

<script>
    export default {
        name: "no-success",
        props: {
            text: {
                type: String,
                default: ''
            },
            height: {
                type: String,
                default: '100%'
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .no-success
        display flex
        align-items center
        justify-content center
        width 100%
        height 100%
        overflow hidden
        p
            opacity 0.7
</style>



